import { BNify } from '../../core'
import { IntegrationClientModel } from '../integration-client.model'
import axios, { Axios, CreateAxiosDefaults } from 'axios'

export class LidoClient implements IntegrationClientModel {
  // Axios instance
  private axios: Axios

  constructor() {
    this.axios = this.initAxios()
  }

  /**
   * Axios initialization
   */
  private initAxios() {
    const options: CreateAxiosDefaults = {
      headers: {
        'content-type': 'application/json',
      },
    }
    return axios.create(options)
  }

  // Methods

  public async getApr(tokenSymbol: string): Promise<number> {
    switch (tokenSymbol) {
      case 'stETH':
        return this.getStETHApr()
      case 'MATIC':
        return this.getMaticApr()
      default:
        return 0
    }
  }

  public async getStETHApr(): Promise<number> {
    return this.axios
      .get('https://eth-api.lido.fi/v1/protocol/steth/apr/last', {
        responseType: 'json',
      })
      .then((response) => {
        if (!response?.data) {
          return 0
        }

        const {
          data: { apr },
        } = response.data

        return BNify(apr).toNumber()
      })
      .catch(() => 0)
  }

  public async getMaticApr(): Promise<number> {
    return this.axios
      .get('https://polygon.lido.fi/api/stats', { responseType: 'json' })
      .then((response) => BNify(response?.data).toNumber())
      .catch(() => 0)
  }
}
